$yellow: #EDC50C;
$green: #20BA61;
$red: #EB5B4C;

* {
  box-sizing: border-box;
}

img {
  width: 100%;
}

.App {
  padding: 0;
  margin: 0;

  .navigation{
    position: relative;
    background-color: #000000;
    color: $yellow;
    padding: 10px;
    .logo{
      font-weight: bolder;
    }
    .fa{
      position: fixed;
      top: 20px;
      right: 10px;
      font-size: 30px;
      padding: 10px;
      background-color: rgba(0, 0, 0,0.4);
    }
    .navbar-nav{
      position: fixed;
      top: 70px;
      width: 100%;
      background-color: #F5D338;
      text-align: center;
      padding: 10px;
      font-size: 1.5em;
      color: #000000;
      li{
        a{
          color: #000000;
          transition: all 300ms ease-in;
          &:hover{
            background-color: #000000;
            color: #ffffff;
            font-size: 1.7em;
          }
        }
        
      }
    }
    .open{
      display: block;
    }
    .close{
      display: none;
    }
  }


  .header{
    display: flex;
    justify-content: center;
    align-items: center;
    .header_text{
    
    }
  }

  .section1 {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 1em;
    background-color: #E5E5E5;

    .sec1_box {
      box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.4);
      text-align: center;
      background-color: #ffffff;
      margin: 1em 0;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .box1_header {
        width: 100%;
        display: flex;
        text-align: center;

        div {
          width: 50%;
          padding: 10px;
        }

        .rental {
          background-color: $yellow;
          padding: 10px;
        }
      }

      input {
        width: 80%;
        margin: 0.5em auto;
        outline: none;
        border: 1px solid #CCCCCC;
        padding: 7px;
      }

      .btn_wrapper {
        width: 100%;
        background-color: $yellow;
        padding: 20px;
        display: flex;
        align-items: center;
        justify-content: center;

        button {
          padding: 8px;
          background-color: #F5D338;
          border: none;
        }
      }
    }

    .sec2_box {
      margin: 1em 0;

      .state {
        display: flex;
        justify-content: space-between;
        background-color: $green;
        padding: 2em;
        color: #ffffff;
        font-weight: bolder;
      }
    }

    .sec3_box {
      margin: 1em 0;

      .state {
        display: flex;
        justify-content: space-between;
        background-color: $red;
        padding: 2em;
        color: #ffffff;
        font-weight: bolder;

      }
    }
  }

  .section2 {
    background-color: #E5E5E5;
    padding: 1em;

    .secLine {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      margin: 2em 0;

      div {
        width: 100%;
        padding: 1px;
        background-color: #000000;
      }

      .sectLine_text {
        width: 100%;
        background-color: inherit;
        text-align: center;
      }
    }

    .d-flex {
      display: flex;
      justify-content: space-around;
      flex-direction: column;
    }

    .cardsec2 {
      width: 100%;
      margin: 2em 0;
      background-color: #ffffff;

      .card_img {}

      .card_text {
        padding: 0 2em;

        p {
          margin-top: 4em;
        }
      }

      .d-flex {
        display: flex;
        justify-content: space-around;
        flex-direction: row;

        div {
          border: 1px solid #DDDDDD;
          width: 34%;
          padding: 10px;
          text-align: center;
          font-size: 22px;
        }
      }
    }

    .sec2_text {
      text-align: center;
      padding: 1em;
    }

  }

  .section3 {
    padding: 1em;

    .secLine {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      margin: 2em 0;

      div {
        width: 100%;
        padding: 1px;
        background-color: #000000;
      }

      .sectLine_text {
        width: 100%;
        background-color: inherit;
        text-align: center;
      }
    }

    .d-flex {
      display: flex;
      justify-content: space-around;
      flex-direction: column;

      .cardsec3 {
        width: 100%;
        margin: 2em 0;
        background-color: #ffffff;

        .card_text_wrapper {
          .texts {
            border: 1px solid #CCCCCC;

            .card_text {
              padding: 0 2em;

              p {
                margin: 4em 0;
              }
            }

            .d-flex {
              display: flex;
              justify-content: space-around;
              flex-direction: row;

              div {
                border: 1px solid #DDDDDD;
                width: 34%;
                padding: 10px;
                text-align: center;
                font-size: 22px;
              }
            }
          }
        }
      }
    }
  }

  .footer {
    background: #000;
    padding: 2em;
    margin-top: 2em;
    color: #ffffff;
    text-align: center;
  }


}





@media (min-width: 500px){
.App {
  padding: 0;
  margin: 0;

  .section1 {
    padding: 1em 7em;
  }

  .section2 {
    padding: 1em 7em;
  }

  .section3 {
    padding: 1em 7em;
  }
}
}

@media (min-width: 750px){
  .App {
    padding: 0;
    margin: 0;

    .navigation{
      position: relative;
      padding: 30px 0;
      padding-left: 80px;
      .logo{
        font-weight: bolder;
        width:200px;
        cursor: pointer;
        h2{
          font-weight: bolder;
        font-size: 2.5em;
        }
        &:hover{
          color: $red;
        }
      }
      .fa{
        display: none;
      }
      .navbar-nav{
        position: absolute;
        top: 10px;
        right: 10px;
        width: 550px;
        background-color: #000000;
        text-align: right;
        padding: 0px;
        display: inline-block;
        li{
          a{
            color: #ffffff;
            transition: none;
            font-size: 1.4em;
            padding: 1.4em 0.3em;
            margin-left: 0px;
            transition: all 200ms ease-in;
            &:hover{
              background-color: $yellow;
              color: #ffffff;
              font-size: 1.4em;
            }
          }
          
        }
      }
    }
  
    .section1 {
      flex-direction: row;
      padding: 1em 5em;
      justify-content: space-around;
     
      .sec1_box {
        width: 30%;
        margin: 1em 0;
      }
      .sec2_box {
        width: 30%;
      }
      .sec3_box {
        width: 30%;
      }
    }
  
    .section2 {
      padding: 1em 5em;
  
      .secLine {
        flex-direction: row;
        justify-content: space-evenly;
        margin: 2em 0;
  
        div {
          width: 25%;
          padding: 1px;
          background-color: #000000;
        }
  
        .sectLine_text {
          width: 40%;
        }
      }
  
      .d-flex {
        display: flex;
        justify-content: space-around;
        flex-direction: row;
      }
  
      .cardsec2 {
        width: 22%;
        margin: 2em 0;
        background-color: #ffffff;
        .d-flex {
          display: flex;
          justify-content: space-around;
          flex-direction: row;
  
          div {
            width: 20%;
            font-size: 22px;
          }
          .travel{
            width: 60%;
          }
        }
      }  
    }
  
    .section3 {
      padding: 1em 5em;
      .secLine {
        flex-direction: row;
        justify-content: space-between;
        margin: 2em 0;
  
        div {
          width: 25%;
        }
  
        .sectLine_text {
          width: 40%;
        }
      }
  
      .d-flex {
        display: flex;
        justify-content: space-around;
        flex-direction: row;
  
        .cardsec3 {
          width: 45%;
          margin: 2em 0;
          .card_text_wrapper {
            display: flex;

            .card_img{
              width: 40%;
              img{
                height: 100%;
              }
            }
            .texts {
              width: 60%;

              .card_text {
                padding: 1em;
                margin: 0;
  
                p {
                  margin: 0em;
                }
              }
  
              .d-flex {
                display: flex;
                justify-content: space-around;
                flex-direction: row;
  
                div {
                  border: 1px solid #DDDDDD;
                  width: 25%;
                  padding: 10px;
                  text-align: center;
                  font-size: 22px;
                }
                .travel{
                  width: 50%;
                }
              }
            }
          }
        }
      }
    }
  
    .footer {
      background: #000;
      padding: 2em;
      margin-top: 2em;
      color: #ffffff;
      text-align: center;
    }
  
  
  }
  }